<template>
  <div>
    <h2>应用1</h2>
    <p>这是应用1的详细信息。</p>
    <a href="/app1">打开应用</a>
  </div>
</template>

<script>
import pwaConfig from './pwa-config';

export default {
  name: 'App1View',
  // 应用1的逻辑,
  created() {
    // 动态设置 manifest.json
    const link = document.createElement('link');
    link.rel = 'manifest';
    link.href = `/manifest/manifest1.json`;
    document.head.appendChild(link);

    // 动态注册 Service Worker
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker
        .register(`/app1/sw_app1.js`)
        .then(() => console.log(`${pwaConfig.name} Service Worker Registered`))
        .catch(err => console.error('Service Worker Registration Failed', err));
    }

    // 动态设置标题
    document.title = pwaConfig.name;
  }
};
</script>

<style>
/* 适当的样式 */
</style>